<script>
import cardVue from './cardVue.vue';
import format from '@/utils/format';
import cartService from '@/service/cart.service';
import commentService from '@/service/comment.service'
export default {
    components:{
        cardVue
    },
    data(){
        return{
            navs:['Gii thiu','Thng s k thut', 'nh gi'],
            activeNav:'Gii thiu',
            star:3,
            activeReply:false, 
            indexReply:null,
            isValid:'',
            inputQuantity:1,
            added:false,
            infoComment:{},
            rate:'',
            selectRate:false,
            comments:[]
        }
    },
    props:['product','spes', 'products', 'typeProduct'], 
    methods:{
        formatPrice(price){
            return format.formatCurrency(price)
        },
        async addproductToCart(id, quantityCart){
            const checkInputQuantity = this.checkInputQuantity(this.product, quantityCart)
            const user = JSON.parse(sessionStorage.getItem('user'))?.user ||{}
            const cartItem = {
                id:id,
                quantityCart:quantityCart,
                typeProduct:this.product.fits? 'accessory':'product'
            }
            if(checkInputQuantity){
                // cha ng nhp
                if(Object.keys(user).length<=0){
                    const cartList=[]
                    if(!this.$cookies.isKey('cart')){
                        cartList.unshift(cartItem)
                        this.$cookies.set('cart', JSON.stringify(cartList))
                    }
    
                    else{
                        let productsInCart = this.$cookies.get('cart')
                        const indexInCart = this.isProductInCart(cartItem,productsInCart)
                        if( indexInCart === -1){
                            productsInCart.unshift(cartItem)
                            this.$cookies.set('cart', JSON.stringify(productsInCart))
                            this.$store.commit('addToCart')
                        }
                        else{
                            productsInCart[indexInCart].quantityCart+=cartItem.quantityCart
                            this.$cookies.set('cart', JSON.stringify(productsInCart))
                            this.$store.commit('addToCart')
                        }
                    }
                    this.added=true
                    setTimeout(()=>{
                        this.added=false
                    }, 1500)
                }
                //  ng nhp
                else{
                    try {
                        const data = {
                            userId:user._id,
                            product:cartItem
                        }
                        const response = await cartService.create(data)
                        if(response.data.status){
                            this.$store.commit('addToCart', response.data.result.products)
                            this.added=true
                            setTimeout(()=>{
                                this.added=false
                            }, 1500)
                        }
                    } catch (error) {
                        console.log(error);
                    }
                }
                
            }
        },
        isProductInCart(newItem, Arrayproduct) {
            let index = -1;
            for (let i = 0; i < Arrayproduct.length; i++) {
                if (Arrayproduct[i].id == newItem.id) {
                    index = i;
                    break;
                }
            }
            return index;
        },
        checkInputQuantity(product, inputQuantity){
            this.isValid=''
            if(inputQuantity<=0 || inputQuantity==''){
                this.isValid='S lng khng hp l'
            }
            else if(inputQuantity> product.inputQuantity){
                this.isValid='S lng trong kho khng '
            }
            else{
                this.isValid=''
            }

            return this.isValid == '' ? true : false
        },
        handleImage(event){
            this.infoComment.image = event.target.files[0] || '';
        },
        async addComment(){
            try {
                this.selectRate=false
                const data ={
                    ...this.infoComment,
                    productId:this.product._id,
                    typeProduct:this.typeProduct,
                    rate:this.rate
                }
                console.log(data);
                if(this.rate==''){
                    this.selectRate=true
                    return;
                }
                else{
                    const response = await commentService.create(data)
                    if(response.data.status){
                        this.infoComment={}
                        this.rate=''
                        this.$refs.fileComment.value=''
                        alert('nh gi ca bn  c gi v s c ph duyt trc khi hin th')
                    }
                    else{
                        alert('nh gi khng thnh cng')
                    }
                }
            } catch (error) {
                console.log(error);
            }
        },
        async getCommentByIdProduct(){
            try {
                const data={
                    productId:this.product?._id,
                    approve:true
                }
                const response = await commentService.findByIdProduct(data)
                if(response.data.status){
                    this.comments=response.data.data
                }
                console.log(response);
            } catch (error) {
                console.log(error);
            }
        },
        changeNav(nav){
            this.activeNav=nav
            if(nav=='nh gi'){
                this.getCommentByIdProduct()
            }
        }
        
    },
    mounted(){
        
    }
}
</script>

