
function print(info = {}) {
  const src= info.image ? 'data:image/jpg;base64,'+ info.image.data : ''
    let productTemplate=''
    info.products.forEach((product, index) => {
        productTemplate+=` 
        <div style="display: flex; font-size: 20px; text-align: center; border: 1px solid #111; padding-bottom: 10px;">
        <div style="width: 8.333333%; margin-top: 10px;">
        ${index+1}. 
     </div>
     <div style="width: 16.6666667%; margin-top: 10px;">
         ${product.idProduct.name}
     </div>
     <div style="width: 16.6666666667%; margin-top: 10px;">
         ${product.priceImport}
     </div>
     <div style="width: 16.6666666667%; margin-top: 10px;">
         ${product.inputQuantity}
     </div>
     <div style="width: 16.6666666667%; margin-top: 10px;">
         ${product.typeProduct=='product' ? 'My photocopy' : 'Ph kin'}
     </div>
     <div style="width: 25%; margin-top: 10px;">
         ${product.total}
     </div>
        </div>
     `
    });

   let print= 
   `
   <div style="height:90%">
        <h4 style=" margin: 20px; font-size: 18px;">S: ${info._id}</h4>
        <div style="width: 100%;">
            <h4 style="text-align: center; font-size: 30px; margin: 0px;">Phiu nhp kho</h4>
            <br>
            <h4 style="text-align: center; font-size: 20px; margin: 0px;">Ngy to phiu: ${info.createdAt}</h4>
        </div>
        <div style="display: flex; justify-content: center; align-items: center;">
            <div style="width: 50%; text-align:center;">
                <h4 style="margin-bottom:10px; margin-left:10px; font-size: 20px">Ngi lp phiu</h4>
                <div  style="margin-bottom:0; margin-left:20px;">
                        <span><b>H v tn: </b>${info.createBy.fullName}</span>
                        <br>
                        <span><b>S in thoi: </b>${info.createBy.phoneNumber}</span>
                </div>
            </div>

            <div style="width: 50%; text-align: center;">
                <h4 style="margin-bottom:10px; margin-left:10px; font-size: 20px">Nh cung cp</h4>
                <div  style="margin-bottom:0; margin-left:20px;">
                        <span><b>Tn: </b>${info.supplier.name}</span>
                        <br>
                        <span><b>a ch: </b>${info.supplier.address}</span>
                        <br>
                        <span><b>S in thoi: </b>${info.supplier.phone}</span>
                </div>
            </div>
        </div>
        <h5 style="text-align: center; font-size: 16px;">Danh sch sn phm</h5>
        <div style="margin: 20px 0; ">
            <div style="display: flex; font-size: 20px; text-align: center; border: 1px solid #111; height: 50px; line-height: 50px;">
                <div style="width: 8.333333%;">
                    STT
                </div>
                <div style="width: 16.66666667%;">
                    Tn Sn phm
                </div>
                <div style="width: 16.6666666667%;">
                    Gi nhp
                </div>
                <div style="width: 16.6666666667%;">
                    S lng
                </div>
                <div style="width: 16.6666666667%;">
                    Loi hng
                </div>
                <div style="width: 25%;">
                    Tng tin
                </div>
            </div>
            ${productTemplate}
        </div>
        <div style="width: 100%;">
            <div style="font-size: 20px; margin-right: 10px; display: flex; justify-content: flex-end;">
                <b>Tng cng</b>: ${info.totalAmount}
            </div>
        </div>
        <div style="margin-top: 40px; height: 150px;">
            <div style="width: 25%; float: left; text-align:center;">
                <b>Ngi giao</b></br>
                (K, h tn)
            </div>
            <div style="width: 25%; float: left; text-align:center;">
                <b>Th kho</b></br>
                (K, h tn)
            </div>
            <div style="width: 25%; float: left; text-align:center;">
                <b>Ngi nhn</b></br>
                (K, h tn)
            </div>
            <div style="width: 25%; float: left; text-align:center;">
                <b>K ton</b></br>
                (K, h tn)
            </div>
        </div>
    </div>
     
`
    if(src!=''){
        print+=`<div style="height:90%">
            <img style="height:auto; width:100%;" src="${src}">
        </div>`
    }

    return print;
}

export default print;
