                    <span>ang x l</span>
                </div>
                <div class="progress-item" style="--left:43% ">
                    <span style="--border-color:#11e911">2</span>
                    <span>ang vn chuyn</span>
                </div>
                <div class="progress-item" style="--left:80%">
                    <span style="--border-color:blue">3</span>
                    <span> giao hng</span>
                </div>
            </div>
