            <div class="btn btn-warning mt-5 mb-1" v-if="order.status ==='ang x l'" @click="update(order._id,{status:'ang vn chuyn'})">Cp nht</div>
            <div class="btn btn-warning mt-5 mb-1" v-else-if="order.status ==='ang vn chuyn'" @click="update(order._id,{status:' giao hng', pricePayed:order.totalAmount})">Cp nht</div>
