                                <td class="col-4 ellipsis">{{ product.nameProduct }}</td>
                                <td class="col-3">{{ product.priceSale }}</td>
                                <td class="col-2">{{ product.quantity }}</td>
                                <td class="col-2">{{ product.typeProduct =='product'? 'My' : 'Ph kin' }}</td>
                            </tr>
                           
                        </tbody>
                    </table>
                </div>
            </div>

            <div class="col-lg-4">
                <div class="order-total">
                    <h5>Chi tit thanh ton</h5>
                    <div class="row text-left">
                        <span class="col-lg-6 mb-2"><strong>Tng tin hng:</strong></span>
                        <span class="col-lg-6 mb-2">{{ order.totalCostOfProducts }}</span>
                        <span class="col-lg-6 mb-2"><strong>Ph vn chuyn:</strong></span>
                        <span class="col-lg-6 mb-2">{{ order.transportFee }}</span>
                        
                        <span class="col-lg-6 mb-2"><strong>Tng cng</strong></span>
                        <span class="col-lg-6 mb-2">{{ formatPrice(order.totalAmount) }}</span>
                        <span class="col-lg-6 mb-2"><strong>S tin  thanh ton</strong></span>
                        <span class="col-lg-6 mb-2">{{ formatPrice(order.pricePayed) }}</span>
                        <span class="col-lg-6 mb-2"><strong>Hnh thc thanh ton</strong></span>
                        <span class="col-lg-6 mb-2">online</span>
                    </div>
                    <div class="btn btn-info me-1" v-if="!order.isPayment && order.status == 'ang x l'" @click="paymentByVnPay(order)">VNPay</div>
                    <div class="btn btn-danger me-1" v-if="!order.isPayment && order.status == 'ang x l'" @click="paymentByMOMO(order)">MOMO</div>
                    <div class="btn btn-success" v-if="!order.isPayment && order.status == 'ang x l'" @click="paymentByCOD(order)">COD</div>
                    <div class="btn btn-warning ms-1" @click="printPDF">In phiu </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import orderService from '@/service/order.service'
import format from '@/utils/format'
import print from '@/utils/print'
import printTemlateOrder from '@/utils/printTemplateOrder'
export default {
    data(){
        return {
            order:{},
        }
    },
    props:['id', 'paymentSuccess'],
    emits:['closeDetail', 'openDetail','closeSuccess'],
    methods:{
        closeDetail(){
            this.$emit('closeDetail')
        },
        closeSuccess(){
            this.$emit('closeSuccess')
        },
        async getOrderById(){
            try {
                const response = await orderService.getById(this.id)
                this.order=response.data
                this.order.createdAt = format.formatDate(this.order.createdAt)
                this.order.totalCostOfProducts= format.formatCurrency( this.order.totalCostOfProducts)
                this.order.VAT= format.formatCurrency( this.order.VAT)
                this.order.transportFee= format.formatCurrency( this.order.transportFee)
                this.order.products.forEach(product =>{
                    product.priceSale=format.formatCurrency(product.priceSale)
                })
            } catch (error) {
                console.log(error);
            }
        },
         formatPrice(price){
            return format.formatCurrency(price)
        },
        async update(id,data){
            try {
                const response =await orderService.update(id,data)
                if(response.data.status){
                    alert("Thay i thnh cng")
                    this.getOrderById()
                }
            } catch (error) {
                console.log(error);
            }
        },
       async paymentByVnPay(order){
            try {
                const data ={
                    totalAmount: order.totalAmount,
                    orderId:order._id
                }
                if(order.status=='Hy n'){
                    alert('n hng  b hy')
                    return ;
                }
                const payment = await orderService.paymentVNPAY('admin/order',data)
                window.location.href=payment.data
            } catch (error) {
                console.log(error);
            }
        },
        async paymentByMOMO(order){
            try {
                const data ={
                    totalAmount: order.totalAmount,
                    orderId:order._id
                }
                if(order.status=='Hy n'){
                    alert('n hng  b hy')
                    return ;
                }
                if(data.totalAmount > 50000000){
                        alert('S tin thanh ton vi momo phi b hn 50 triu')
                        return;
                    }
                const payment = await orderService.paymentMOMO('admin/order',data)
                window.location.href=payment.data
            } catch (error) {
                console.log(error);
            }
        },
        async paymentByCOD(order){
            try {
                if(confirm(`S tin phi tr l :${order.totalAmount}`)){
                    const data ={
                        totalAmount:order.totalAmount,
                        paymentMethod:'COD',
                        pricePayed:order.totalAmount,
                        isPayment:true
                    }
                    const response =await orderService.update(order._id,data)
                    if(response.data.status){
                        this.getOrderById()
                    }
                }
            } catch (error) {
                console.log(error);
            }
        },
        async printPDF(){
            const printTemplate= printTemlateOrder( this.order)
            print(printTemplate,'Phiu n hng' )
        },
        
    },
    mounted(){
        this.getOrderById()
    }
}
</script>

