<template>
    <div>
        <h3 class="h3">Bng sn phm</h3>
        <div class="input-admin-filter">
            <form class="filter-admin-date" @submit.prevent.stop="filterDate">
                <select name="" id="" v-model="dateFilter.day" required>
                    <option value="0">Ngy</option>
                    <option v-for="day in 31" :key="day" :value="day">{{ day }}</option>
                </select>
                <select name="" id="" v-model="dateFilter.month" required>
                    <option value="0">Thng</option>
                    <option v-for="month in 12" :value="month" :key="month">Thng {{ month }}</option>
                </select>
                <select name="" id="" v-model="dateFilter.year" required>
                    <option value="0">Nm</option>
                    <option v-for="year in years" :key="year" :value="year">{{ year }}</option>
                </select>
                <select name="" id="" v-model="dateFilter.field" required>
                    <option value="">Chn kiu</option>
                    <option value="0">Tt c</option>
                    <option value="createdAt">Ngy To</option>
                    <option value="updatedAt">Ngy cp nht</option>
                    <option value="dateEntyReceipt">Ngy Nhp kho</option>
                </select>
                <button>Lc</button>
            </form>
            <div class="admin-export">
                <div class="btn btn-danger" @click="chart">
                    <i class="fa-solid fa-chart-line"></i> Chart
                </div>
                <div class="btn btn-success" @click="exportToExcel">
                    <i class="fa-solid fa-file-excel"></i> Excel
                </div>
                <div class="btn btn-warning" @click="exportToPDF"><i class="fa-solid fa-file-pdf"></i> Pdf</div>
            </div>
        </div>
        <table class="table-product">
            <thead class="table-head">
                <tr>
                    <th class="STT">STT</th>
                    <th class="column1-th text-center">
                        <div class="admin-table-title">
                            <span v-if="isSort" @click="offSort" class="active-sort"> Lc </span>
                            <p>Tn sn phm</p>
                            <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(1, 'name')">T A n Z</li>
                                    <li @click="sort(-1, 'name')">T Z v A</li>
                                    <div class="filter-admin-input">
                                        <input
                                            type="text"
                                            placeholder="Tm sn phm theo tn"
                                            v-model="filterInput"
                                            @input="handleFitter('name')"
                                        />
                                    </div>
                                </ul>
                            </div>
                        </div>
                    </th>
                    <th class="column2">
                        <div class="admin-table-title">
                            <p>S lng nhp</p>
                            <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(1, 'inputQuantity')">T nh n ln</li>
                                    <li @click="sort(-1, 'inputQuantity')">T ln n nh</li>
                                    <li @click="sort(0, 'inputQuantity')">Bng 0</li>
                                    <div class="filter-admin-input">
                                        <input
                                            type="text"
                                            placeholder="Tm sn phm theo tn"
                                            v-model="filterInput"
                                            @input="handleFitter('inputQuantity')"
                                        />
                                    </div>
                                </ul>
                            </div>
                        </div>
                    </th>
                    <th class="column3">
                        <div class="admin-table-title">
                            <p>S lng bn</p>
                            <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(1, 'soldQuantity')">T nh n ln</li>
                                    <li @click="sort(-1, 'soldQuantity')">T ln n nh</li>
                                    <li @click="sort(0, 'soldQuantity')">Bng 0</li>
                                    <div class="filter-admin-input">
                                        <input
                                            type="text"
                                            placeholder="Tm sn phm theo tn"
                                            v-model="filterInput"
                                            @input="handleFitter('soldQuantity')"
                                        />
                                    </div>
                                </ul>
                            </div>
                        </div>
                    </th>
                    <th class="column6">
                        <div class="admin-table-title">
                            <p>Gi nhp</p>
                            <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(1, 'priceImport')">T nh n ln</li>
                                    <li @click="sort(-1, 'priceImport')">T ln n nh</li>
                                    <li @click="sort(0, 'priceImport')">Bng 0</li>
                                    <div class="filter-admin-input">
                                        <input
                                            type="text"
                                            placeholder="Tm sn phm theo tn"
                                            v-model="filterInput"
                                            @input="handleFitter('priceImport')"
                                        />
                                    </div>
                                </ul>
                            </div>
                        </div>
                    </th>
                    <th class="column7">
                        <div class="admin-table-title">
                            <p>Gi bn</p>
                            <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(1, 'priceSale')">T nh n ln</li>
                                    <li @click="sort(-1, 'priceSale')">T ln n nh</li>
                                    <div class="filter-admin-input">
                                        <input
                                            type="text"
                                            placeholder="Tm sn phm theo tn"
                                            v-model="filterInput"
                                            @input="handleFitter('priceSale')"
                                        />
                                    </div>
                                </ul>
                            </div>
                        </div>
                    </th>
                    <th class="column2">
                        <div class="admin-table-title">
                            <p>S lng thu</p>
                            <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(1, 'rentalQuantity')">T nh n ln</li>
                                    <li @click="sort(-1, 'rentalQuantity')">T ln n nh</li>
                                    <li @click="sort(0, 'rentalQuantity')">Bng 0</li>
                                    <div class="filter-admin-input">
                                        <input
                                            type="text"
                                            placeholder="Tm sn phm theo tn"
                                            v-model="filterInput"
                                            @input="handleFitter('rentalQuantity')"
                                        />
                                    </div>
                                </ul>
                            </div>
                        </div>
                    </th>
                    <th class="column8">
                        <div class="admin-table-title">
                            <p>Gi thu</p>
                            <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(1, 'priceRental')">T nh n ln</li>
                                    <li @click="sort(-1, 'priceRental')">T ln n nh</li>
                                    <div class="filter-admin-input">
                                        <input
                                            type="text"
                                            placeholder="Tm sn phm theo tn"
                                            v-model="filterInput"
                                            @input="handleFitter('priceRental')"
                                        />
                                    </div>
                                </ul>
                            </div>
                        </div>
                    </th>
                    <th class="column4-th">
                        <div class="admin-table-title">
                            <p>lch s</p>
                            <!-- <div class="admin-sort">
                                <i class="fa-solid fa-sort"></i>
                                <ul class="admin-sort-list">
                                    <li @click="sort(-1, 'createdAt')">Mi nht</li>
                                    <li @click="sort(1, 'createdAt')">C</li>
                                </ul>
                            </div> -->
                        </div>
                    </th>
                    <th class="column5-th text-center">
                        <span class="me-4 text-left">Xem</span>
                        <span class="me-4 text-left">Sa</span>
                        <span class="">Thm</span>
                    </th>
                </tr>
            </thead>
            <tbody class="table-body">
                <tr v-for="(product, index) in products" :key="product._id">
                    <td class="STT"> {{index+1}}</td>
                    <td class="column1">
                        {{ product.name }}
                    </td>
                    <td class="column2">{{ product.inputQuantity }}</td>
                    <td class="column3">{{ product.soldQuantity }}</td>
                    <td class="column6">{{ formatCurency(product.priceImport) }}</td>
                    <td class="column7">{{ formatCurency(product.priceSale) }}</td>
                    <td class="column3">{{ product.rentalQuantity || 0 }}</td>

                    <td class="column8">{{ formatCurency(product.priceRental) }}</td>
                    <td class="column4">
                        <span><b>To:</b> {{ formatDateNoTime(product.createdAt) }}</span>
                        <!-- <span><b>Sa:</b> {{ formatDateNoTime(product.updatedAt) }}</span> -->
                        <span><b>Nhp:</b> {{ !!product.dateEntyReceipt ? formatDateNoTime(product.dateEntyReceipt)  : 'Cha nhp'}}</span>
                    </td>
                    <td class="column5">
                        <div class="btn btn-outline-info me-3" @click="handleProductDetail(product._id)">
                            <i class="fa-solid fa-circle-info"></i>
                        </div>
                        <router-link
                            :to="{ name: 'product.edit', params: { id: product?._id } }"
                            class="btn btn-outline-warning me-3"
                        >
                            <i class="fa-solid fa-pen-to-square"></i>
                        </router-link>
                        <div class="btn btn-outline-success me-3" @click="handleProductSpe(product._id)">
                            <i class="fa-solid fa-plus"></i>
                        </div>
                    </td>
                </tr>
            </tbody>
        </table>
        
        <div class="product-pagination">
            <ul class="pagination">
                <li>
                    <span @click="previousPage"><i class="fa-solid fa-angles-left"></i></span>
                </li>
                <li><span>...</span></li>
                <li v-for="index in lengthPage" :key="index">
                    <span :class="{ active_page: activePage === index }" @click="handlePage(index)">{{ index }}</span>
                </li>

                <li><span>...</span></li>
                <li>
                    <span @click="nextPage"><i class="fa-solid fa-angles-right"></i></span>
                </li>
            </ul>
        </div>
        <!--  product detail -->
        <admin-product-detail
            :activeProductDetail="activeProductDetail"
            :id="id"
            @closeProductDetail="closeProductDetail"
        ></admin-product-detail>
        <admin-specification-vue
            v-if="isActiveSpe"
            :isActiveSpe="isActiveSpe"
            :idProduct="id"
            @closeSpe="isActiveSpe = false"
        ></admin-specification-vue>

        <div class="overlay" v-if="activeChart" @click="closeChart">
            <div class="chart2" @click.stop>
                <h3>Biu  thng k</h3>
                <div class="chart-container">
                    <admin-chart
                    class="chart-size"
                    v-if="activeChart"
                    :data="data"
                    :dynamic-labels="label"
                    :chart-type="'bar'"
                    :year="new Date().getFullYear()"
                    :month="new Date().getMonth()+1"
                    :typeStatistical="'month'"
                    :color="'#F55050'"
                    :name="'S lng bn v tn kho'"
                    :data2="data2"
                    :label1="'S lng bn v thu'"
                    :label2="'S lng tn kho'"
                    ></admin-chart>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import productService from '@/service/product.service';
import adminProductDetail from '@/components/admin/adminProductDetail.vue';
import adminSpecificationVue from './adminSpecification.vue';
import format from '@/utils/format';
import printProduct from '@/utils/printTemplateProduct'
import exportToExcel from '@/utils/exportToExcel';
import adminChart from './adminChart.vue';
export default {
    components: {
        adminProductDetail,
        adminSpecificationVue,
        adminChart
    },
    data() {
        return {
            data:[],// s lng bn
            data2:[],// s lng nhp
            label:[],// tn di
            activeChart:false,
            activePage: 1,
            products: [],
            lengthPage: 1,
            activeProductDetail: false,
            isActiveSpe: false,
            id: '',
            pageNumber: 1,
            pageSize: 10,
            isSort: false,
            type: '',
            field: '',
            filterInput: '',
            years: [],
            idTimeOut: null,
            dateFilter: {
                day: '0',
                month: '0',
                year: '0',
                field: '',
            },
        };
    },
    watch:{
        activeChart(){
           if(this.activeChart==false){
            this.data=[]
            this.data2=[]
            this.label=[]
           }
        }  
    },
    methods: {
        closeChart(){
            this.data=[]
            this.data2=[]
            this.activeChart=false
        },
        chart(){
            this.data=[]
            this.data2=[]
            this.label=[]
            this.products.forEach((product)=>{
                this.data.push(product.soldQuantity+product.rentalQuantity)
                this.label.push(product.name)
            })
            this.products.forEach((product)=>{
                this.data2.push(product.inputQuantity)
            })
           this.activeChart=true
           
        },
        handlePage(index) {
            this.activePage = index;
            this.pageNumber = index;
            this.getproducts();
            this.isSort = false;
        },
        handleProductDetail(id) {
            this.activeProductDetail = true;
            this.id = id;
        },
        handleProductSpe(id) {
            this.isActiveSpe = true;
            this.id = id;
        },
        nextPage() {
            if (this.pageNumber >= this.lengthPage) {
                alert('Trang cui');
            } else {
                this.pageNumber += 1;
                this.activePage += 1;
                this.getproducts();
                this.sort(this.type, this.field);
            }
        },
        previousPage() {
            if (this.pageNumber <= 1) {
                alert('Trang u');
            } else {
                this.pageNumber -= 1;
                this.activePage -= 1;
                this.getproducts();
                this.sort(this.type, this.field);
            }
        },
        closeProductDetail() {
            this.activeProductDetail = false;
            this.id = '';
        },
        offSort() {
            this.getproducts();
            this.isSort = false;
        },
        formatCurency(price) {
            return format.formatCurrency(price);
        },
        formatDateNoTime(date) {
            return format.formatDateNoTime(date);
        },
        async getproducts() {
            try {
                const length = await productService.getProducts();
                this.lengthPage = Math.ceil(length.data.length / this.pageSize);
                const response = await productService.getProducts(this.pageNumber, this.pageSize);
                this.products = response.data;
            } catch (error) {
                console.log(error);
            }
        },
        async sort(type, field) {
            this.type = type;
            this.field = field;
            this.isSort = true;
            if (field === 'name') {
                this.products.sort((a, b) => {
                    if (type == 1) {
                        return a[field].localeCompare(b[field]);
                    } else if (type == -1) {
                        return b[field].localeCompare(a[field]);
                    }
                });
            } else {
                // sp xp
                this.products.sort((a, b) => {
                    if (type == 1) {
                        return parseFloat(a[field]) - parseFloat(b[field]);
                    } else if (type == -1) {
                        return parseFloat(b[field]) - parseInt(a[field]);
                    }
                });
                // ly ra bng 0
                if (type === 0) {
                    const productEqualZero = this.products.filter((product) => product[field] === 0);
                    this.products = [];
                    this.products = [...productEqualZero];
                }
            }
        },
        getYears() {
            const yearsTarget = new Date().getFullYear();
            for (let i = 5; i >= 1; i--) {
                this.years.push(yearsTarget - i);
            }
            this.years.push(yearsTarget);
            
        },
        handleFitter(name) {
            clearTimeout(this.idTimeOut);
            this.getproducts()
            this.idTimeOut = setTimeout(() => {
                this.filter(name);
            }, 500);
        },
        filter(name) {
            const regex = new RegExp(this.filterInput.trim(), 'i');
            const abc = this.products.filter((product) => {
                if (name === 'name') {
                    return regex.test(product[name]);
                } else {
                    return product[name] >= this.filterInput;
                }
            });
            this.products = abc;
        },
        async exportToExcel() {

            exportToExcel(this.products,'San_pham')
            // const response = await productService.exportExcel({ data: this.products });
            // const blob = new Blob([response.data], {
            //     type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
            // });
            // const url = window.URL.createObjectURL(blob);
            // const link = document.createElement('a');
            // link.href = url;
            // link.setAttribute('download', 'example.xlsx');
            // document.body.appendChild(link);
            // link.click();
        },

        async exportToPDF(){
            const template= printProduct(this.products)
            const response = await productService.exportPDF({ data: template }, 'vertical');
            const blob = new Blob([response.data], {
                type: 'application/pdf',
            });
            const url = window.URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', 'SanPham.pdf');
            document.body.appendChild(link);
            link.click();
        },
        async filterDate() {
            const { day, month, year, field } = this.dateFilter;
            const response = await productService.filterByDate(day, month, year, field, this.pageNumber, this.pageSize);
            this.products = [...response.data];
        },
    },
    mounted() {
        this.getproducts();
        this.getYears();
    },
};
</script>

<style scoped>
    .chart2{
        padding: 10,0;
        width: 1300px;
        height: 680px;
        background: #fff;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .STT{
        width: 35px;
        text-align: center;
    }
    .chart-container{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .chart-size{
        width: 90%;
    }
</style>
