import format from "./format";
function print(info = {}) {
  const src= info.image ? 'http://localhost:3000/'+info.image :''
    let productTemplate=''
    info?.accessorys.forEach((product, index) => {
        productTemplate+=` 
        <div style="display: flex; font-size: 20px; text-align: center; border: 1px solid #111; padding-bottom: 10px;">
        <div style="width: 8.333333%; margin-top: 10px;">
        ${index+1}. 
     </div>
     <div style="width: 20%; margin-top: 10px;">
         ${product.nameProduct}
     </div>
     <div style="width: 20%; margin-top: 10px;">
         ${product.priceSale}
     </div>
     <div style="width: 20%; margin-top: 10px;">
         ${product.quantity}
     </div>
     <div style="width: 20%; margin-top: 10px;">
        
    </div>
        </div>
     `
    });

   const print= 
   `
   <div style="height:100%">
        <h4 style=" margin: 20px; font-size: 18px;">S: ${info._id}</h4>
        <div style="width: 100%;">
            <h4 style="text-align: center; font-size: 30px; margin: 0px;">Ha n</h4>
            <h4 style="text-align: center; font-size: 20px; margin: 0px;">Ngy thc hin: ${format.formatDateNoTime(info.startDate)}</h4>
            <h4 style="text-align: center; font-size: 20px; margin: 0px;">Dch v: ${info.serviceId?.name}</h4>
        </div>
        <div style="display: flex; justify-content: center; align-items: center;">
            <div style="width: 50%; text-align:center;">
                <h4 style="margin-bottom:10px; margin-left:10px; font-size: 20px">Nhn vin</h4>
                <div  style="margin-bottom:0; margin-left:20px;">
                        <span><b>H v tn: </b>${info.staffId?.fullName}</span>
                        <br>
                        <span><b>S in thoi: </b>${info.staffId?.phone}</span>
                </div>
            </div>

            <div style="width: 50%; text-align: center;">
                <h4 style="margin-bottom:10px; margin-left:10px; font-size: 20px">Thng tin khch hng</h4>
                <div  style="margin-bottom:0; margin-left:20px;">
                        <span><b>Tn: </b>${info.nameCustomer}</span>
                        <br>
                        <span><b>a ch: </b>${info.address}</span>
                        <br>
                        <span><b>S in thoi: </b>${info.phone}</span>
                </div>
            </div>
        </div>
        <h5 style="text-align: center; font-size: 16px;margin-top:10px;">Danh sch ph kin</h5>
        <div style="margin: 20px 0; ">
            <div style="display: flex; font-size: 20px; text-align: center; border: 1px solid #111; height: 50px; line-height: 50px;">
                <div style="width: 8.333333%;">
                    STT
                </div>
                <div style="width: 20%;">
                    Tn Sn phm
                </div>
                <div style="width: 20%;">
                    Gi
                </div>
                <div style="width: 20%;">
                    S lng
                </div>
                <div style="width: 20%;">
                    Ghi ch
                </div>
            </div>
            ${productTemplate}
        </div>
        <div style="width: 100%;">
            <div style="font-size: 20px; margin-right: 10px; display: flex; justify-content: flex-end; ">
                <div>
                    <b>Tin cng</b>: ${format.formatCurrency(info.wage)}
                    <br>
                    <b>Tng cng</b>: ${format.formatCurrency(info.totalOfAccessory)}
                    <br>
                    <b>Tng cng</b>: ${format.formatCurrency(info.totalAmount)}
                    <br>
                </div>
            </div>
        </div>
    </div>
    <div style="height:90% ">
        <h5 style="text-align:center; font-size:20px;">nh bo co thc t</h5>
        <img style="height:auto; width:100%;" src="${src}">
    </div>
`

    return print;
}

export default print;
