<template>
    <div class="overlay" >
            <div class="admin-card-acc">
                <i class="fa-solid fa-xmark card-close" @click="closeProductDetail"></i>
                <div class="card-image">
                    <div  class="card-image-head"><span class="card-info-title">nh ph kin</span></div>
                    <img   :src="'http://localhost:3000/'+accessory.image" alt="">
                </div>
                <div class="card-info">
                    <h3>{{ accessory.name }}</h3>
                    <div class="row">
                        <div class="col-lg-4">
                            <span class="card-info-title">Thng tin ph kin</span>
                            <div class="card-info-price">
                                <p>Gi nhp: <span>{{ accessory.priceImport }}</span></p>
                                <p>S lng nhp: <span>{{ accessory.inputQuantity }}</span></p>
                                <p>Gi bn: <span>{{ accessory.priceSale }}</span></p>
                                <p>S lng bn: <span>{{ accessory.soldQuantity }}</span></p>
                                <p>Loi: <span>{{ accessory.idType?.name }}</span></p>
                                <p>Hng sn xut: <span>{{ accessory.idBrand?.name }}</span></p>
                                <p>Ngy sa: <span>{{ accessory.updatedAt }}</span></p>
                                <p>Ngy sa: <span>{{ accessory.updatedAt }}</span></p>
                            </div>
                        </div>
                        <div class="col-lg-8">
                            <span class="card-info-title">Ph hp vi cc sn phm</span>
                            <div class="card-info-price info-acc">
                                <span v-if="accessory.fits?.length<=0">ang  cp</span>
                                <span class="text-left mb-2" v-for="(fit, index) in accessory.fits" :key="fit._id"><b class="text-black">Tn sn phm {{ index+1 }}</b>: <span>{{ fit.product.name }}</span></span>
                            </div>
                            
                        </div>
                        <div class="col-lg-12">
                            <span class="card-info-title mt-3">M t </span>
                            <div class="card-info-description">
                                {{ accessory.description }}
                            </div>
                        </div>
                    </div>
                </div>
              
            </div>
    </div>
</template>

<script>
import format from '@/utils/format';
import accessoryService from '@/service/accessory.service';
export default {
    data(){
        return {
            accessory:{},
        }
    },
    watch:{
        activeProductDetail(){
            this.getProductDetail()
            this.getSpeByIdProduct()
        }
    },
    props:{
        idAcc:String
    },
    emits:['closeDetail'],
    methods:{
        closeProductDetail(){
            this.$emit('closeDetail')
        },
        async getAccessoryDetail(){
            try {
                const response = await accessoryService.getById(this.idAcc)
                this.accessory={
                    ...response.data
                }
                console.log(this.accessory.fits);
                this.accessory.createdAt = format.formatDate(this.accessory.createdAt)
                this.accessory.updatedAt = format.formatDate(this.accessory.updatedAt)
                
            } catch (error) {
                console.log(error);
            }
        },
    },
    mounted(){
        this.getAccessoryDetail()
    }
}
</script>

<style scoped> 
.info-acc span{
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    width: 100%;
    text-align: left;
}

.info-acc{
    height: 326px;
    overflow-y: scroll;
    scroll-behavior: smooth;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    margin-right: 20px;
}
</style>