<template>
    <div>
        <h3 class="h3">Thm sn phm</h3>
        <form class="form-product row" action="" @submit.prevent.stop="addProduct" enctype="multipart/form-data">
            <span class="mes mess-success" v-if="!!addProductSuccess">{{ addProductSuccess }}</span>
            <span class="mes mes-failded" v-if="!!addProductFail">{{ addProductFail }}</span>
            <div class="overlay" v-if="loadingProduct">
                <div class="loading-add-product">
                    <div class="lds-dual-ring"></div>
                </div>
            </div>
            <div class="group col-lg-6">
                <label for="">Tn Sn phm <span class="required">*</span> </label>
                <input name="name-product" v-model="infoProduct.name" required placeholder="Nhp tn sn phm" type="text" class="product-input">
            </div>
            <div class="group col-lg-6">
                        <label for="form-file">nh sn phm <span class="required">*</span></label>
                        <input @change="handleSelectFile" ref="inputFile" required class="formFile" type="file" id="form-file" >
            </div>
           <!-- loi -->
           <div class="group d-flex col-lg-4">
               <div class="d-flex mb-1">
                    <label for="">Loi sn phm <span class="required">*</span></label>
                    <span class="btn btn-info btn-brand" @click="activeFormType=true">Mi</span>
               </div>
               <!-- Them loi mi -->
               <div class="brand">
                   <div class="overlay2" v-if="activeFormType">
                        <form action="" class="brand-form" @submit.prevent.stop="addType">
                            <div class="close-form" @click="closeFormType">
                                <i class="fa-solid fa-xmark"></i>
                            </div>
                            <div class="form-head me-5 ms-3">
                                <h2 class="title">Thm thng hiu</h2>
                                <span class="mes-success" v-if="!!mesSuccess">{{ mesSuccess }}</span>
                                <span class="mes-failed" v-if="!!mesFaild">{{ mesFaild }}</span>
                                <div class="lds-dual-ring" v-if="loading"></div>
                            </div>
                            <div class="group">
                                <label for="">Tn loi <span class="required">*</span></label>
                                <input v-model="infoType.name" type="text" required placeholder="Nhp tn thng hiu">
                            </div>
                            <div>
                                <button class="btn btn-brand-submit">Thm</button>
                            </div>
                       </form>
                   </div>
               </div>
                <select name="" id="" class="form-select" v-model="infoProduct.typeId" required>
                    <option value="" class="form-option">--- Chn loi ---</option>
                    <option v-for="(type, index) in types" :key="index" class="form-option" :value="type._id">{{ type.name }}</option>
                </select>
            </div>
            <!-- thng hiu -->
            <div class="group d-flex col-lg-4">
               <div class="d-flex mb-1">
                    <label for="">Thng hiu sn phm <span class="required">*</span></label>
                    <span class="btn btn-info btn-brand" @click="activeFormbrand=true">Mi</span>
               </div>
               <!-- Them thuong hieu mi -->
               <div class="brand">
                   <div class="overlay2" v-if="activeFormbrand">
                        <form action="" class="brand-form" @submit.prevent.stop="addBrand">
                            <div class="close-form" @click="closeFormBrand">
                                <i class="fa-solid fa-xmark"></i>
                            </div>
                            <div class="form-head me-5 ms-3">
                                <h2 class="title">Thm thng hiu</h2>
                                <span class="mes-success" v-if="!!mesSuccess">{{ mesSuccess }}</span>
                                <span class="mes-failed" v-if="!!mesFaild">{{ mesFaild }}</span>
                                <div class="lds-dual-ring" v-if="loading"></div>
                            </div>
                            <div class="group">
                                <label for="">nh thng hiu <span class="required">*</span></label>
                                <input @change="handleBrand" ref="fileBrand" type="file">
                            </div>
                            <div class="group">
                                <label for="">Tn thng hiu <span class="required">*</span></label>
                                <input v-model="infoBrand.name" type="text" required placeholder="Nhp tn thng hiu">
                            </div>
                            <div>
                                <button class="btn btn-brand-submit">Thm</button>
                            </div>
                       </form>
                   </div>
               </div>
                <select name="" id="" class="form-select" v-model="infoProduct.brandId" required>
                    <option value="" class="form-option">--- Chn thng hiu ---</option>
                    <option v-for="brand in brands" :key="brand._id" class="form-option" :value="brand._id">{{ brand.name }}</option>
                </select>
            </div>
            <!-- danh mc -->
            <div class="group d-flex col-lg-4">
               <div class="d-flex mb-1">
                    <label for="">Danh mc sn phm <span class="required">*</span></label>
                    <span class="btn btn-info btn-brand" @click="activeFormCategory=true">Mi</span>
               </div>
               <!-- Them danh mc mi -->
               <div class="brand">
                   <div class="overlay2" v-if="activeFormCategory">
                        <form action="" class="brand-form" @submit.prevent.stop="addCategory">
                            <div class="close-form" @click="closeFormCategory">
                                <i class="fa-solid fa-xmark"></i>
                            </div>
                            <div class="form-head me-5 ms-3">
                                <h2 class="title">Thm Danh mc</h2>
                                <span class="mes-success" v-if="!!mesSuccess">{{ mesSuccess }}</span>
                                <span class="mes-failed" v-if="!!mesFaild">{{ mesFaild }}</span>
                                <div class="lds-dual-ring" v-if="loading"></div>
                            </div>
                            <div class="group">
                                <label for="">Tn danh mc <span class="required">*</span></label>
                                <input v-model="infoCategory.name" type="text" required placeholder="Nhp tn danh mc">
                            </div>
                            <div>
                                <button class="btn btn-brand-submit">Thm</button>
                            </div>
                       </form>
                   </div>
               </div>
                <select name="" id="" class="form-select" v-model="infoProduct.categoryId" required>
                    <option value="" class="form-option">--- Chn danh mc ---</option>
                    <option v-for="category in categories" :key="category._id" class="form-option" :value="category._id">{{ category.name }}</option>
                </select>
            </div>
            <div class="group col-lg-4 mt-1">
                <label for="">Gi bn sn phm <span class="required">*</span></label>
                <input name="price-sale" v-model="infoProduct.priceSale" required placeholder="Nhp gi bn" type="number" class="product-input">
                <span v-if="!!valid.priceSale" :class="{ 'text-danger': !!valid.priceSale }">{{
                    valid.priceSale
                }}</span>
            </div>
            <div class="group col-lg-4 mt-1">
                <label for="">Gi thu sn phm <span class="required">*</span> </label>
                <input name="price-rental" v-model="infoProduct.priceRental" required placeholder="Nhp gi cho thu/Ngy" type="number" class="product-input">
                <span v-if="!!valid.priceRental" :class="{ 'text-danger': !!valid.priceRental }">{{
                    valid.priceRental
                }}</span>
            </div>
            <div class="group col-lg-4 mt-1">
                <label for="">S thng bo hnh <span class="required">*</span> </label>
                <input name="price-rental" v-model="infoProduct.warrantyTime" required placeholder="Nhp s thng bo hnh" type="number" class="product-input">
                <span v-if="!!valid.warrantyTime" :class="{ 'text-danger': !!valid.warrantyTime }">{{
                    valid.warrantyTime
                }}</span>
            </div>
            <div class="group col-lg-12">
                <label for="">M t</label>
                <textarea v-model="infoProduct.description"  required placeholder="M t v sn phm" class="col-lg-12 form-description" name="" id="" cols="30" rows="4"></textarea>
                
            </div>
            <div><button class="btn form-btn">Thm</button></div>
        </form>
    </div>
</template>

<script>
import productSevice from '@/service/product.service'
import brandService from '@/service/brand.service'
import categoryService from '@/service/category.service'
import typeService from '@/service/type.service'
export default {
    data(){
        return{
            infoProduct:{
                image:null,
                brandId:"",
                categoryId:"",
                typeId:"",
            },
            infoCategory:{},
            activeFormbrand:false,
            activeFormCategory:false,
            activeFormType:false,
            infoBrand:{},
            infoType:{},
            mesSuccess:'',
            mesFaild:'',
            addProductSuccess:'',
            addProductFail:'',
            loading:false,
            loadingProduct:false,
            brands:[],
            categories:[],
            types:[],
            valid:{}
        }
    },
    methods:{
        closeFormCategory(){
            this.activeFormCategory=false
            this.mesFaild=''
            this.mesSuccess=''
            this.infoCategory={}
        },
        closeFormBrand(){
            this.activeFormbrand=false
            this.mesFaild=''
            this.mesSuccess=''
            this.infoBrand={}
        },
        closeFormType(){
            this.activeFormType=false
            this.mesFaild=''
            this.mesSuccess=''
            this.infoType={}
        },
        handleSelectFile(event){
            this.infoProduct.image = event.target.files[0] || '';
        },
        handleBrand(event){
            this.infoBrand.image = event.target.files[0] || '';
        },
        validateForm(info){
            if(info.priceSale <0){
                this.valid.priceSale='Gi bn khng c nh hn 0'
            }
            else{
                this.valid.priceSale=false
            }

            if(info.priceRental <0){
                this.valid.priceRental='Gi thu khng c nh hn 0'
            }
            else{
                this.valid.priceRental=false
            }
            if(info.warrantyTime <0){
                this.valid.warrantyTime='S thng khng c nh hn 0'
            }
            else{
                this.valid.warrantyTime=false
            }
            let isValid = false;
            const arrayValid= Object.values(this.valid)
            for(let i=0;i<arrayValid.length;i++){
                if(arrayValid[i]===false){
                    isValid = true;
                } else {
                    isValid = false;
                    break
                }
            }
            return isValid;
            
        },
        async addProduct(){
            try {
                const isValid = this.validateForm(this.infoProduct)
                if(isValid){
                    this.loadingProduct=true
                    const response = await productSevice.create(this.infoProduct)
                    console.log(this.infoProduct);
                    this.loadingProduct=false
                    if(response.data.status){
                        this.addProductSuccess=response.data.mes
                        this.addProductFail=''
                        this.infoProduct={
                            image:null,
                            brandId:"",
                            categoryId:"",
                            typeId:""
                        }
                        this.$refs.inputFile.value=''
                    }
                    else{
                        this.addProductFail=response.data.mes
                        this.addProductSuccess=''
                    }
                }
            } catch (error) {
                console.log(error);
            }
        },
        async addBrand(){
            try {
                this.loading=true
                const response = await brandService.create(this.infoBrand)
                this.loading=false
                if(response.data.status){
                    this.mesSuccess=response.data.mes
                    this.mesFaild=''
                    this.infoBrand={}
                    this.$refs.fileBrand.value=''
                }
                else{
                    this.mesFaild=response.data.mes
                    this.mesSuccess=''
                }
                this.getBrands()
            } catch (error) {
                console.log(error);
            }
        },
        async addType(){
            try {
                this.loading=true
                const response = await typeService.create(this.infoType)
                this.loading=false
                if(response.data.status){
                    this.mesSuccess=response.data.mes
                    this.mesFaild=''
                    this.infoType={}
                }
                else{
                    this.mesFaild=response.data.mes
                    this.mesSuccess=''
                }
                this.getType()
            } catch (error) {
                console.log(error);
            }
        },
        async getBrands(){
            try {
                const response = await brandService.getAll()
                this.brands=[...response.data]
            } catch (error) {
                console.log(error);
            }
        },
        async addCategory(){
                try {
                this.loading=true
                
                const response = await categoryService.create(this.infoCategory)
                this.loading=false
                if(response.data.status){
                    this.mesSuccess=response.data.mes
                    this.mesFaild=''
                    this.infoCategory={}
                }
                else{
                    this.mesFaild=response.data.mes
                    this.mesSuccess=''
                }
                this.getCategories()
            } catch (error) {
                console.log(error);
            }
        },
        async getCategories(){
            try {
                const response = await categoryService.getAll()
                this.categories=[...response.data]
            } catch (error) {
                console.log(error);
            }
        },
        async getType(){
            try {
                const response = await typeService.getAll()
                this.types=[...response.data]
            } catch (error) {
                console.log(error);
            }
        }
    },
    mounted(){
        this.getBrands()
        this.getCategories()
        this.getType()
    }
}
</script>

