<template>
    <div class="overlay" v-if="activeProductDetail">
            <div class="admin-card">
                <i class="fa-solid fa-xmark card-close" @click="closeProductDetail"></i>
                <div class="card-image">
                    <div v-if="activeImg" class="card-image-head"><span class="card-info-title">nh sn phm</span></div>
                    <img  v-if="activeImg"  :src="'http://localhost:3000/'+product.image" alt="">
                    <div  v-if="!activeImg" class="card-specifications">
                        <i class="fa-solid fa-xmark card-close-speci" @click="activeImg=true"></i>
                        <span class="card-specifications-title" >Thng s k thut</span>
                        <div v-if="existSpe" class="card-info-name">
                            <p>Tc  in: <span>{{ infoSpe.speed }}</span></p>
                            <p>Khay giy <span>{{ infoSpe.paperTray }}</span></p>
                            <p>Kch thc giy <span>{{ infoSpe.paperSize }}</span></p>
                            <p>Cng ngh: <span>{{ infoSpe.connectionTechnology }}</span></p>
                            <p>Mn hnh: <span>{{ infoSpe.screen }}</span></p>
                        </div>
                        <div class="div" v-else>
                            <span>Cha thm thng s k thut</span>
                        </div>
                    </div>
                </div>
                <div class="card-info">
                    <h3>{{ product.name }}</h3>
                    <span class="card-info-title">Thng tin sn phm</span>
                    <div class="card-info-name">
                        <p>Tn thng hiu: <span>{{product.brandId? product.brandId.name : '' }}</span></p>
                        <p>Tn danh mc: <span>{{product.categoryId? product.categoryId.name : '' }}</span></p>
                        <p>Loi: <span>{{ product.typeId? product.typeId.name : '' }}</span></p>
                    </div>
                    <div class="card-info-price">
                        <p>Gi nhp: <span>{{ product.priceImport }}</span></p>
                        <p>S lng nhp: <span>{{ product.inputQuantity }}</span></p>
                        <p>Gi bn: <span>{{ product.priceSale }}</span></p>
                        <p>S lng bn: <span>{{ product.soldQuantity }}</span></p>
                        <p>Gi cho thu: <span>{{ product.priceRental }}</span></p>
                        <p>Ngy to: <span>{{ product.createdAt }}</span></p>
                        <p>Ngy sa: <span>{{ product.updatedAt }}</span></p>
                    </div>
                    <span class="card-info-title">M t </span>
                    <div class="card-info-description">
                        {{ product.description }}
                    </div>

                    <div class="card-control mt-3">
                        <button class="btn btn-info me-3" @click="toggleImg">Thng s k thut</button>
                        <router-link :to="{name:'product.edit', params:{id:id}}" class="btn btn-warning" >Chnh Sa</router-link> 
                    </div>
                </div>
              
            </div>
    </div>
</template>

<script>
import productService from '@/service/product.service';
import specificationsService from '@/service/specifications.service';
import format from '@/utils/format';
export default {
    data(){
        return {
            activeImg:true,
            product:{},
            specificatios:{},
            infoSpe:{},
            existSpe:true
        }
    },
    watch:{
        activeProductDetail(){
            this.getProductDetail()
            this.getSpeByIdProduct()
        }
    },
    props:{
        activeProductDetail:Boolean,
        id:String
    },
    emits:['closeProductDetail'],
    methods:{
        toggleImg(){
            this.activeImg=false
            console.log(this.id);
        },
        closeProductDetail(){
            this.$emit('closeProductDetail')
        },
        async getProductDetail(){
            try {
                const response = await productService.getProductDetail(this.id)
                if(response.status){
                    this.product=response.data
                    this.product.priceImport=format.formatCurrency(this.product.priceImport)
                    this.product.priceRental=format.formatCurrency(this.product.priceRental)
                    this.product.priceSale=format.formatCurrency(this.product.priceSale)
                    this.product.createdAt=format.formatDate(this.product.createdAt)
                    this.product.updatedAt=format.formatDate(this.product.updatedAt)
                }
            } catch (error) {
                console.log(error);
            }
        },
        async getSpeByIdProduct(){
            try {
                const response = await specificationsService.findByIdProduct(this.id)
                if(response.data.status){
                    this.infoSpe={
                        ...response.data.data
                    }
                    this.existSpe=true

                }
                else{
                    this.existSpe=false
                }
            } catch (error) {
                console.log(error);
            }
        }
    },
}
</script>

