<template>
    <div class="overlay" v-if="paymentSuccess" @click="closeSuccess">
        <div class="modal-success text-success">
            <div class="text-center success-icon">
                <span><i class="fa-solid fa-check"></i></span>
            </div>
            <h3 class="">Thanh ton thnh cng</h3>
        </div>
    </div>
    <div class="overlay" v-if="paymentSuccess ==false" @click="closeSuccess">
        <div class="modal-success text-danger">
            <div class="text-center success-icon">
                <span><i class="fa-solid fa-check"></i></span>
            </div>
            <h3 class="">Thanh ton khng thnh cng</h3>
        </div>
    </div>
    <div class="admin-create-order">
        <h3>Thm n hng thu</h3>
        <form class="admin-form-order" ref="formOrder" @submit.prevent.stop="addOrder">
            <div class="row">
                <div class="group col-lg-4">
                    <div class="d-flex mb-1">
                        <label for="">Tn Khch hng <span class="required">*</span></label>
                        <span class="btn btn-info btn-brand" @click="activeCustomer = true">Mi</span>
                    </div>
                    <!-- Them khch hng mi -->
                        <div class="overlay" v-if="activeCustomer">
                            <form action="" class="customer-form" @submit.prevent.stop="addCustomer">
                                <div class="close-form-customer" @click="closeFormCustomer">
                                    <i class="fa-solid fa-xmark"></i>
                                </div>
                                <div class="form-head">
                                    <h2 class="title">Thm khch hng</h2>
                                    <span class="mes-success" v-if="!!mesSucCus">{{ mesSucCus }}</span>
                                    <span class="mes-failed" v-if="!!mesFaiCus">{{ mesFaiCus }}</span>
                                    <div class="lds-dual-ring" v-if="loading"></div>
                                </div>
                                <div class="row">
                                    <div class="spe-group col-lg-6">
                                        <label for="">Tn khch hng <span class="required">*</span></label>
                                        <input
                                            type="text"
                                            required
                                            placeholder="Nhp tn khch hng"
                                            v-model="customer.fullName"
                                        />
                                    </div>
                                    <div class="spe-group col-lg-6">
                                        <label for="">S in thoi<span class="required">*</span></label>
                                        <input
                                            type="text"
                                            required
                                            placeholder="Nhp S in thoi"
                                            v-model="customer.phone"
                                        />
                                        <span v-if="!!valid.phone" :class="{ 'text-danger': !!valid.phone }">{{
                                            valid.phone
                                        }}</span>
                                    </div>
                                    <div class="spe-group col-lg-12">
                                        <label for="">a ch<span class="required">*</span></label>
                                        <input
                                            type="text"
                                            required
                                            placeholder="Nhp tn ng, hm, s nh"
                                            v-model="customer.address"
                                        />
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="group col-lg-4">
                                        <div class="d-flex mb-1">
                                            <label for="">Tnh/Thnh ph <span class="required">*</span></label>
                                        </div> 
                                        <select v-model="VmodelAddress.city" name="" id="" class="form-select  w-100" @change="getDistricts(VmodelAddress.city)" required>
                                            <option value="" class="form-option">--- Chn ---</option>
                                            <option v-for="item in city" :key="item.code" :value="item">{{ item.name }}</option>
                                        </select>
                                    </div>
                                    <div class="group col-lg-4">
                                        <div class="d-flex mb-1">
                                            <label for="">Qun/huyn <span class="required">*</span></label>
                                        </div>
                                        <select v-model="VmodelAddress.district" name="" id="" class="form-select  w-100" @change="getWards(VmodelAddress.district)" required>
                                            <option value="" class="form-option">--- Chn ---</option>
                                            <option v-for="item in districts" :key="item.code" :value="item">{{ item.name }}</option>
                                        </select>
                                    </div>
                                    <div class="group col-lg-4">
                                        <div class="d-flex mb-1">
                                            <label for="">Phng/X <span class="required">*</span></label>
                                        </div>
                                        <select v-model="VmodelAddress.ward" name="" id="" class="form-select w-100" @change="saveAddress(VmodelAddress.ward)" required>
                                            <option value="" class="form-option">--- Chn ---</option>
                                            <option v-for="item in wards" :key="item.code" :value="item">{{ item.name }}</option>
                                        </select>
                                    </div>
                                </div>
                                <div>
                                    <button class="btn btn-brand-submit mt-5">Thm</button>
                                </div>
                            </form>
                        </div>
                    <select v-model="customerId" class="form-select" required @change="getCustomerById(customerId)">
                        <option value="" class="form-option">--- Chn khch hng ---</option>
                        <option v-for="(customer, index) in customers" :key="index" class="form-option" :value="customer._id">
                            {{ customer.fullName }}
                        </option>
                    </select>
                     <!-- edit khch hng -->
                     <div class="overlay" v-if="activeEditCustomer">
                        <form action="" class="customer-form" @submit.prevent.stop="editCustomer">
                            <div class="close-form-customer" @click="closeFormCustomerEdit">
                                <i class="fa-solid fa-xmark"></i>
                            </div>
                            <div class="form-head">
                                <h2 class="title">Xem v thay i thng tin</h2>
                                <span class="mes-success" v-if="!!mesSucCus">{{ mesSucCus }}</span>
                                <span class="mes-failed" v-if="!!mesFaiCus">{{ mesFaiCus }}</span>
                            </div>
                            <div class="row">
                                <div class="spe-group col-lg-6">
                                    <label for="">Tn khch hng <span class="required">*</span></label>
                                    <input
                                        type="text"
                                        required
                                        placeholder="Nhp tn khch hng"
                                        v-model="customerInfo.fullName"
                                    />
                                </div>
                                <div class="spe-group col-lg-6">
                                    <label for="">S in thoi<span class="required">*</span></label>
                                    <input
                                        type="text"
                                        required
                                        placeholder="Nhp S in thoi"
                                        v-model="customerInfo.phone"
                                    />
                                    <span v-if="!!valid.phone" :class="{ 'text-danger': !!valid.phone }">{{
                                        valid.phone
                                    }}</span>
                                </div>
                                <div class="spe-group col-lg-12" v-if="!activeEditAddress">
                                    <label for="">a ch<span class="required">*</span> 
                                    </label>
                                    <input
                                        type="text"
                                        required
                                        placeholder="Nhp tn ng, hm, s nh"
                                        v-model="customerInfo.address"
                                        disabled
                                        />
                                </div>
                                <span class="col-lg-3 ms-2 btn btn-warning" @click="activeEditAddress=!activeEditAddress">{{activeEditAddress ? 'Gi a ch c' : 'To a ch mi'}}</span>
                                <div class="spe-group col-lg-12" v-if="activeEditAddress">
                                    <label for="">a ch<span class="required">*</span></label>
                                    <input
                                        type="text"
                                        required
                                        placeholder="Nhp tn ng, hm, s nh"
                                        v-model="customerInfo.address2"
                                    />
                                </div>
                            </div>
                            <div class="row" v-if="activeEditAddress">
                                <div class="group col-lg-4">
                                    <div class="d-flex mb-1">
                                        <label for="">Tnh/Thnh ph <span class="required">*</span></label>
                                    </div> 
                                    <select v-model="VmodelAddress.city" name="" id="" class="form-select  w-100" @change="getDistricts(VmodelAddress.city)" required>
                                        <option value="" class="form-option">--- Chn ---</option>
                                        <option v-for="item in city" :key="item.code" :value="item">{{ item.name }}</option>
                                    </select>
                                </div>
                                <div class="group col-lg-4">
                                    <div class="d-flex mb-1">
                                        <label for="">Qun/huyn <span class="required">*</span></label>
                                    </div>
                                    <select v-model="VmodelAddress.district" name="" id="" class="form-select  w-100" @change="getWards(VmodelAddress.district)" required>
                                        <option value="" class="form-option">--- Chn ---</option>
                                        <option v-for="item in districts" :key="item.code" :value="item">{{ item.name }}</option>
                                    </select>
                                </div>
                                <div class="group col-lg-4">
                                    <div class="d-flex mb-1">
                                        <label for="">Phng/X <span class="required">*</span></label>
                                    </div>
                                    <select v-model="VmodelAddress.ward" name="" id="" class="form-select w-100" @change="saveAddress(VmodelAddress.ward)" required>
                                        <option value="" class="form-option">--- Chn ---</option>
                                        <option v-for="item in wards" :key="item.code" :value="item">{{ item.name }}</option>
                                    </select>
                                </div>
                            </div>
                            <div>
                                <button class="btn btn-brand-submit mt-5">Sa</button>
                            </div>
                        </form>
                    </div>
                </div>
                <div class="group col-lg-8">
                    <div class="w-100">
                       <div class="d-flex ms-2"> <label for="">Ghi ch</label></div>
                        <textarea class="note" v-model="note" placeholder="Ghi ch cho n hng"></textarea>
                    </div>
                </div>
                <!-- danh sach san pham -->
                <div class="croll">
                    <span class="d-block mt-3">Danh sch sn phm</span>
                    <div class="row" v-for="(orderProduct, index) in ordersProducts" :key="index">
                        <div class="group col-lg-4">
                            <div class="d-flex mb-1">
                                <label for="" class="mt-0">Danh sch hng <span class="required">*</span></label>
                            </div>
                            <select v-model="orderProduct.productId" class="form-select" @change="getproductById(products,orderProduct.productId,index)" required>
                                <option value="" class="form-option">--- Chn ---</option>
                                <option  v-for="product in products" :value="product._id" :key="product._id">{{ product.name }}</option>
                            </select>
                        </div>
                        <div class="spe-group col-lg-4">
                                <label  for="" class="mt-0 mb-0">Gi bn</label>
                                <span class="price">Gi: {{ orderProduct.priceRental }}</span>
                        </div>
                        <div class="spe-group col-lg-4">
                                <label  for="" class="mt-0 mb-0">S lng<span class="text-danger ms-2">*</span></label>
                                <input v-model="orderProduct.quantity" @keyup="(event)=>checkQuantity(event,orderProduct)"  required type="number" placeholder="Nhp s lng hng" />
                        </div>
                        
                    </div>
                    
                </div>
                <div class="row mb-2">
                    <div class="col-3 d-flex justify-content-center align-items-center mt-1">
                        <div class="group me-3">
                            <span class="btn btn-outline-success" @click="pushRow">Thm dng</span>
                        </div>
                        <div class="group">
                            <span class="btn btn-outline-danger" @click="popRow">Bt dng</span>
                        </div>
                    </div>
                </div>
                <!--  cc thng tin-->
                <div class="row">
                    <div class="spe-group col-lg-3">
                        <label  for="" class="mt-0 mb-0">S thng thu<span class="text-danger ms-2">*</span></label>
                       <input class="mt-1" v-model="quantityMonth" @keyup="(event)=>checkQuantityMonth(event)"  required type="number" placeholder="Nhp s thng thu" />
                   </div>
                   <div class="spe-group col-lg-3">
                        <label  for="" class="mt-0 mb-0">Cch thanh ton<span class="text-danger ms-2">*</span></label>
                        <select v-model="payinfull" class="form-select mb-0 mt-1" @change="totalProduct">
                            <option value="" class="form-option">--- Chn cch thanh ton ---</option>
                            <option :value="true" class="form-option">Thanh ton ton b</option>
                            <option :value="false" class="form-option">Thanh ton theo thng</option>
                        </select>
                    </div>
                    <div class="spe-group col-2">
                        <label  for="" class="mt-0">Tng tin sn phm</label>
                        <input type="text" v-model="totalCostOfProducts" disabled>
                    </div>
                    
                    <div class="spe-group col-2">
                        <label  for="" class="mt-0">Tng cng</label>
                        <input type="text" v-model="totalAmount" disabled>
                    </div>
                    <div class="spe-group col-2">
                        <label  for="" class="mt-0">S tin thanh ton</label>
                        <input type="text" v-model="pricePayed" disabled>
                    </div>
                </div>
                <!-- hnh thc thanh ton -->
                <div class="row mt-4">
                    <div class="col-lg-4">
                        <label for="payment" :class="{'payment-active': isClassCod, 'payment-error':choosePayent}" class="payment" @click=" paymentMeThod('COD')">
                                <img src="https://s3-ap-southeast-1.amazonaws.com/pharmacity-ecm-asm-dev/payment-method/cash.webp" alt="">
                                <div class="payment-content">
                                    <span>Tin mt</span>
                                    <span>Thanh ton khi nhn hng</span>
                                </div>
                                <div class="payment-checkbox">
                                    <span></span>
                                </div>
                        </label>
                    </div>
                    <div class="col-lg-4">
                        <label for="payment" :class="{'payment-active': isClassOnlVNPAY, 'payment-error':choosePayent}" class="payment"  @click=" paymentMeThod('VNPAY')">
                                <img src="https://vnpay.vn/s1/statics.vnpay.vn/2023/9/06ncktiwd6dc1694418196384.png" alt="">
                                <div class="payment-content">
                                    <span>VNPay</span>
                                    <span>Thanh ton qua v VNPay</span>
                                </div>
                                <div class="payment-checkbox">
                                    <span></span>

                                </div>
                        </label>
                    </div>
                    <div class="col-lg-4">
                        <label for="payment" :class="{'payment-active': isClassOnlMOMO, 'payment-error':choosePayent}" class="payment"  @click=" paymentMeThod('MOMO')">
                            <img src="https://upload.wikimedia.org/wikipedia/vi/f/fe/MoMo_Logo.png" alt="">
                            <div class="payment-content">
                                <span>MOMO</span>
                                <span>Qut m QR momo</span>
                            </div>
                            <div class="payment-checkbox">
                                <span></span>
                            </div>
                    </label>
                    </div>
                </div>
            </div>
            <button class="btn btn-success mt-3">To n hng</button>
        </form>
    </div>
</template>

<script>
import axios from 'axios';
import customerService from '@/service/customer.service'
import productService from '@/service/product.service';
import rentalService from '@/service/rental.service';
export default {
    data(){
        return{
            activeCustomer:false,
            VmodelAddress:{city:'',district:'',ward:''},
            city:[],
            districts:[],
            wards:[],
            address:{},
            customer:{},
            valid:{},
            mesFaiCus:'',
            mesSucCus:'',
            customers:[],
            products:[],
            accessorys:[],
            product:{},
            ordersProducts:[{
                productId:'',
                priceRental:0
            }],
            customerId:'',
            isSubmit:true,
            paymentMethod:'',
            isClassCod:false,
            isClassOnlVNPAY:false,
            isClassOnlMOMO:false,
            choosePayent:false,
            paymentSuccess:null,
            note:'',
            totalCostOfProducts:0,
            totalAmount:0,
            VAT:0,
            quantityMonth:null,
            customerInfo:{},
            activeEditAddress:false,
            activeEditCustomer:false,
            payinfull:'',
            pricePayed:0
        }
    },
    watch:{
        quantityMonth(){
            if(this.payinfull.toString()!=''){
                this.totalProduct()
            }
        }
    },
    methods:{
        closeSuccess(){
            this.$router.push('/admin/rental-create')
            this.paymentSuccess=null
        },
        totalProduct(){
            
            this.totalCostOfProducts=0
            this.totalAmount=0
            this.pricePayed=0
            this.ordersProducts.forEach(product =>{
                this.totalCostOfProducts+=product.priceRental*product.quantity||0
            })
            this.totalAmount=(this.totalCostOfProducts*this.quantityMonth)||0
            if(this.payinfull){
                this.pricePayed=this.totalAmount
            }
            else{
                this.pricePayed=Math.ceil(this.totalAmount/this.quantityMonth)
            }
        },
        isPaymentSuccess() {
            const params = new URLSearchParams(window.location.search);
            if(params.get('success') === 'true'){
                this.paymentSuccess= true
            }
            else if(params.get('success') === 'false'){
                this.paymentSuccess= false
            }
        },
        async getCity(){
            const res = await axios.get('https://provinces.open-api.vn/api/')
            this.city=[...res.data]
        },
        async getDistricts(info){
            const code=info.code
            const res = await axios.get(`https://provinces.open-api.vn/api/p/${code}/?depth=2`)
            this.districts=[...res.data.districts],
            this.address.city=info.name
        },
        async getWards(info){
            const code=info.code
            this.address.district=info.name
            const res = await axios.get(`https://provinces.open-api.vn/api/d/${code}/?depth=2`)
            this.wards=[...res.data.wards]
        },
        saveAddress(info){
            this.address.ward=info.name
        },
        closeFormCustomer(){
            this.activeCustomer=false
            this.city=[]
            this.districts=[]
            this.wards=[],
           this.VmodelAddress={city:'',district:'',ward:''}
        },
        async addCustomer(){
            try {
                const isValid = this.validateForm(this.customer)
                const data={
                    ...this.customer
                }
                data.address=this.customer.address+','+this.address.ward+','+this.address.district+','+this.address.city
                if(isValid){
                    const response = await customerService.create(data)
                    if(response.data.status){
                        this.mesSucCus=response.data.mes
                        this.mesFaiCus=''
                        this.VmodelAddress={city:'',district:'',ward:''}
                        this.customer={}
                        this.getAllCustomers()
                    }
                    else{
                        this.mesFaiCus=response.data.mes
                        this.mesSucCus=''
                    }
                }
            } catch (error) {
                console.log(error);
            }
            
        },
        validateForm(infoCustomer) {
            const phoneRegex = /^[0-9]+$/;
            if (!phoneRegex.test(infoCustomer.phone) || infoCustomer.phone.length < 10) {
                this.valid.phone = 'S in thoi khng hp l';
            } else {
                this.valid.phone = undefined;
            }

            let isValid = false;
            const arrayValid= Object.values(this.valid)
            for(let i=0;i<arrayValid.length;i++){
                if(arrayValid[i]===undefined){
                    isValid = true;
                } else {
                    isValid = false;
                    break
                }
            }
            return isValid;
        },
        async getAllCustomers(){
            try {
                const response = await customerService.getAll()
                this.customers=[...response.data]
            } catch (error) {
                console.log(error);
            }
        },
        async getAllProducts(){
            try {
                const response = await productService.getProducts()
                response.data.filter(product =>product.inputQuantity>0)
                this.products=response.data.filter(product =>product.inputQuantity>0)
            } catch (error) {
                console.log(error);
            }
        },
        
        async editCustomer(){
            try {
                const isValid = this.validateForm(this.customerInfo)
                const data={
                    ...this.customerInfo
                }
                data.address=this.activeEditAddress ? 
                    this.customerInfo.address2+', '+this.address.ward+', '+this.address.district+', '+this.address.city :
                    this.customerInfo.address
                if(isValid){
                    const response = await customerService.update(this.customerInfo._id,data)
                    if(response.data.status){
                        this.mesSucCus=response.data.mes
                        this.mesFaiCus=''
                        this.VmodelAddress={city:'',district:'',ward:''}
                        this.getCustomerById(this.customerInfo._id)
                        this.activeEditAddress=false
                    }
                    else{
                        this.mesFaiCus=response.data.mes
                        this.mesSucCus=''
                    }
                }
            } catch (error) {
                console.log(error);
            }
            
        },
        getproductById(products,id,index){
            products.map(product => {
                if(product._id === id){
                    this.ordersProducts[index].priceRental=product.priceRental
                    this.ordersProducts[index].priceImport=product.priceImport
                    this.ordersProducts[index].inputQuantity=product.inputQuantity
                    this.ordersProducts[index].nameProduct=product.name
                    this.ordersProducts[index].quantity=null
                    this.totalAmount=0
                    this.totalCostOfProducts=0
                }
                else{
                    return;
                }
            })
        },
        pushRow(){
            this.ordersProducts.push({
                productId:'',
                priceSale:0
            })
        },
        popRow(){
            if(this.ordersProducts.length <=1){
                alert('Dng cui')
            }
            else{
                this.ordersProducts.pop()
            }
        },
        async getCustomerById(id){
            try {
                const response = await customerService.getById(id)
                this.customerInfo = response.data
                this.activeEditCustomer=true
            } catch (error) {
                console.log(error);
            }
        },
        closeFormCustomerEdit(){
            this.activeEditCustomer=false
            this.activeEditAddress=false
            this.city=[]
            this.districts=[]
            this.wards=[],
           this.VmodelAddress={city:'',district:'',ward:''}
        },
        async addOrder(){
            try {
                const user = JSON.parse(sessionStorage.getItem('user'))
                if(this.paymentMethod ==''){
                    this.isSubmit=false
                    this.choosePayent=true
                }
                else{
                    this.isSubmit=true
                }
                const customer = await customerService.getById(this.customerId)
                
                const data={
                    createBy:user ? user.user._id : null,
                    customerId:this.customerId,
                    address:customer.data.address,
                    phone:customer.data.phone,
                    nameCustomer:customer.data.fullName,
                    products:[
                        ...this.ordersProducts
                    ],
                    totalCostOfProducts:this.totalCostOfProducts,
                    paymentMethod:this.paymentMethod,
                    totalAmount:this.totalAmount,
                    priceMonth:Math.ceil(this.totalAmount/this.quantityMonth),
                    payInFull:this.payinfull,
                    note:this.note,
                    quantityMonth:this.quantityMonth
                }
                if(this.isSubmit){
                    if(this.paymentMethod =='VNPAY'){
                        const response = await rentalService.create(data)
                        const resutl = response.data.data
                        if(response.data.status){
                            const payment = await rentalService.paymentVNPAY('admin/rental-create',{totalAmount: this.payinfull ? resutl.totalAmount : resutl.priceMonth, orderId:resutl._id})
                            window.location.href=payment.data
                        }
                    }
                    else if(this.paymentMethod =='MOMO'){
                        const response = await rentalService.create(data)
                        const resutl = response.data.data
                        if(response.data.status){
                            const payment = await rentalService.paymentMOMO('admin/rental-create',{totalAmount: this.payinfull ? resutl.totalAmount : resutl.priceMonth, orderId:resutl._id})
                            window.location.href=payment.data
                        }
                    }
                    else{
                        const response = await rentalService.create(data)
                        if(response.data.status){
                            this.product={}
                            this.ordersProducts=[{
                                productId:'',
                                priceSale:0,
                            }],
                            this.customerId='',
                            this.isSubmit=true,
                            this.isClassCod=false
                            this.isClassOnl=false
                            this.totalAmount=0
                            this.totalCostOfProducts=0
                            this.quantityMonth=null
                            this.note=''
                            alert(response.data.mes)
                            
                        }
                        else{
                            alert(response.data.mes)
                        }
                    }
                }
                else{
                    alert('Kim tra li d liu  nhp')
                }
            } catch (error) {
                console.log(error);
            }
            
        },
        checkQuantity(e,product){
            if(e.target.value <=0 || e.target.value > product.inputQuantity){
                e.target.style.borderColor='red'
                this.isSubmit=false
                this.totalAmount=0
                this.totalCostOfProducts=0
            }
            else{
                if(this.payinfull !=''){
                    this.totalProduct()
                }
                e.target.style.borderColor='#0E8388'
                this.isSubmit=true
            }
        },
        checkQuantityMonth(e){
            if(e.target.value <=0 ){
                e.target.style.borderColor='red'
                this.isSubmit=false
                
            }
            else{
                e.target.style.borderColor='#0E8388'
                this.isSubmit=true
            }
        },
        async paymentMeThod( type){
            this.choosePayent=false
            if(type=='COD'){
                this.isClassCod=!this.isClassCod
                if(this.isClassCod){
                    this.paymentMethod='COD'
                    this.isClassOnlVNPAY=false
                    this.isClassOnlMOMO=false
                }
                else{
                    this.paymentMethod=''
                }
            }
            else if(type == 'VNPAY'){
                this.isClassOnlVNPAY=!this.isClassOnlVNPAY
                if(this.isClassOnlVNPAY){
                    this.paymentMethod='VNPAY'
                    this.isClassCod=false
                    this.isClassOnlMOMO=false
                }
                else{
                    this.paymentMethod=''
                }
            }
            else if(type == 'MOMO'){
                this.isClassOnlMOMO=!this.isClassOnlMOMO
                if(this.isClassOnlMOMO){
                    this.paymentMethod='MOMO'
                    this.isClassCod=false
                    this.isClassOnlVNPAY=false

                }
                else{
                    this.paymentMethod=''
                }
            }
        }
    },
    mounted(){
        // this.getCity()
        this.getAllCustomers()
        this.isPaymentSuccess()
        this.getAllProducts()
    }
};
</script>

