<template>
  <div class="overlay">
    <form class="spe-form" action="" @submit.prevent.stop="{}">
        <i class="fa-solid fa-xmark spe-close " @click="closeSpe"></i>
        <p>Thng s k thut</p>
        <span class="text-danger" v-if="mesFail">{{ mesFail }}</span>
        <span class="text-success" v-if="mesSuccess">{{ mesSuccess }}</span>

        <div class="row">
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Tc </label>
                    <input required v-model="infoSpe.speed" type="text" placeholder="Nhp tc ">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Kch thc giy</label>
                    <input required v-model="infoSpe.paperSize" type="text" placeholder="Nhp Kch thc giy">
                </div>
            </div>

            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Khay giy</label>
                    <input required v-model="infoSpe.paperTray" type="text" placeholder="Nhp Khay giy">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Mn hnh</label>
                    <input required v-model="infoSpe.screen" type="text" placeholder="Nhp Mn hnh">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Cng ngh</label>
                    <input required v-model="infoSpe.connectionTechnology" type="text" placeholder="Nhp Cng ngh">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for=""> phn gii</label>
                    <input required v-model="infoSpe.screenResolution" type="text" placeholder="Nhp  phn gii">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">B nh ram</label>
                    <input required v-model="infoSpe.ramMemory" type="text" placeholder="Nhp B nh ram">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for=""> cng</label>
                    <input required v-model="infoSpe.hardDrive" type="text" placeholder="Nhp  cng">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Thi gian khi ng</label>
                    <input required v-model="infoSpe.bootTime" type="text" placeholder="Nhp Thi gian khi ng">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Phng to thu nh</label>
                    <input required v-model="infoSpe.zoom" type="text" placeholder="Nhp Phng to thu nh">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Sao chp lin tc</label>
                    <input required v-model="infoSpe.continuously" type="text" placeholder="Nhp sao chp lin tc">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Cng kt ni</label>
                    <input required v-model="infoSpe.connector" type="text" placeholder="Nhp Cng kt ni">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">H iu hnh</label>
                    <input required v-model="infoSpe.operatingSystem" type="text" placeholder="Nhp H iu hnh">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">in nng tiu th</label>
                    <input required v-model="infoSpe.electricUsed" type="text" placeholder="Nhp in nng tiu th">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Cng sut</label>
                    <input required v-model="infoSpe.wattage" type="text" placeholder="Nhp Cng sut">
                </div>
            </div>
            <div class="col-lg-4">
                <div class="spe-group">
                    <label for="">Trng lng</label>
                    <input required v-model="infoSpe.weight" type="text" placeholder="Nhp Trng lng">
                </div>
            </div>
        </div>
       
       
       
        <div class="btn btn-outline-success" v-if="!create" @click="createSpe">Sa</div>
        <div class="btn btn-outline-success" v-else @click="createSpe">Thm</div>
    </form>
  </div>
</template>

<script>
import specificationService from '@/service/specifications.service'
export default {
    data(){
        return{
            infoSpe:{},
            create:true,
            mesSuccess:'',
            mesFail:''
        }
    }, 
    props:{
        isActiveSpe:Boolean,
        idProduct:String
    },
    emits:['closeSpe'],
    methods:{
        closeSpe(){
            this.$emit('closeSpe')
        },
        async getByIdProduct(){
            try {
                const response = await specificationService.findByIdProduct(this.idProduct)
                if(response.data.status && Object.keys(response.data.data).length >0){
                    this.infoSpe={
                        ...response.data.data
                    }
                    this.create=false
                }
            } catch (error) {
                console.log(error);
            }
        },
        async createSpe(){
            try {
                console.log(Object.keys(this.infoSpe), this.create);
                if(Object.keys(this.infoSpe).length > 0){
                    this.infoSpe.idProduct=this.idProduct
                    if(this.create){
                        const response = await specificationService.create(this.infoSpe)
                        if(response.data.status){
                            this.mesSuccess=response.data.mes
                            this.mesFail=''
                            this.getByIdProduct()
                        }
                        else{
                            this.mesSuccess=''
                            this.mesFail=response.data.mes
                        }
                    }
                    else{
                        const response = await specificationService.update(this.infoSpe._id,this.infoSpe)
                        if(response.data.status){
                            this.mesSuccess=response.data.mes
                            this.mesFail=''
                            this.infoSpe=response.data.data
                        }
                        else{
                            this.mesSuccess=''
                            this.mesFail=response.data.mes
                        }
                    }
                }
                else{
                    this.mesSuccess=''
                    this.mesFail='Cha nhp d liu'
                }
            } catch (error) {
                console.log(error);
            }
        }
    },
    mounted(){
        this.getByIdProduct()
    }
}
</script>

<style scoped>

</style>